
.item {
    width: 17vw;
}
.item img
{
    width: 17vw;
}
.item p {
    margin: 6px 0px;
    font-size: 15px;
}

.item-prices {
    display: flex;
    gap: 10px;
}

.item-price-new{
    color: #374151;
    font-size: 15px;
    font-weight: 600;
}

.item-price-old{
    color: #8c8c8c;
    font-size: 15px;
    font-weight: 500;
    text-decoration: line-through;
}

.item :hover{
    transform: scale(1.3);
    transition: 0.6s;
}